
// HEADER
.header {
	flex: 0 0 auto;
	height: 80px;
	&-logo {
		width: 150px;
	}
	&-dropdown {
		position: relative;
		&-content {
			display: none;
			position: absolute;
			right: 0;
			top: 25px;
			border-radius: 0;
			// box-shadow: 0 1px 4px 1px rgb(0 0 0 / 8%);
			background: #fff;
			min-width: 160px;
			z-index: 1;
		}
	}
	&-menu {
		color: #33475b;
		font-size: 16px;
		font-family: LatoWeb,Helvetica,Arial,sans-serif;
		&:hover {
			text-decoration: underline;
			color: #33475b;
		}
	}
	&-link {
		background: #30c39e;
		padding: 5px 15px;
		border-radius: 3px;
		color: #fff;
		&:hover {
			text-decoration: underline;
			color: #fff;
		}
	}
}
.menu_item {
	color: #fff;
	font-size: 12px;
	&_drop {
		cursor: pointer;
	}
}
.burger {
	height: 25px;
	width: 30px;
	position: relative;
	transition: .5s ease-in-out;
	cursor: pointer;
    z-index: 1;
	span {
		display: block;
		position: absolute;
		height: 4px;
		width: 100%;
		background: $text;
		border-radius: 4px;
		opacity: 1;
		left: 0;
		transition: .25s ease-in-out;
		&:nth-child(1) {
  			top: 0px;
		}
		&:nth-child(2),
		&:nth-child(3) {
			top: 10px;
		}
		&:nth-child(4) {
			top: 20px;
		}
	}
	&.active {
		span {
			&:nth-child(1) {
				top: 10px;
				width: 0%;
				left: 50%;
			}
			&:nth-child(2) {
				transform: rotate(45deg);
			}
			&:nth-child(3) {
				transform: rotate(-45deg);
			}
			&:nth-child(4) {
				top: 10px;
				width: 0%;
				left: 50%;
			}
		}
	}
}
