  color: #fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  font-family: "Roboto", sans-serif;
  color: #4E4E4E;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

h1, h2, h3, h4 {
  font-weight: normal;
}

img, svg {
  max-height: 100%;
  max-width: 100%;
  width: auto;
  height: auto;
}

input, button, textarea {
  outline: none;
}

input:focus, button:focus, textarea:focus {
  outline: none;
}

input, textarea {
  padding: 10px;
}

textarea {
  resize: none;
}

ul {
  list-style: none;
  margin-bottom: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  color: #33475b;
}

button, .btn {
  border: 2px solid #33475b;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  border-radius: 10px;
  min-width: 120px;
  background: #33475b;
  color: #ffffff;
  position: relative;
  overflow: hidden;
}

button:hover, .btn:hover {
  background: #33475b;
  border-color: #33475b;
  color: #ffffff;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.15);
}

.animate, a, button, .btn {
  transition: all 0.3s ease-out;
}

.no_scroll {
  overflow: hidden;
}

.no_scroll:after {
  content: '';
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1;
}

.hidden {
  display: none !important;
}

.img_cover {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
  width: 100%;
}

